.device-emulator {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.device-emulator section {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 32px;
  border-bottom: 1px lightgray solid;
  margin-bottom: 16px;
  padding-bottom: 24px;
  box-sizing: content-box;
}

.device-emulator select, .device-emulator input {
  outline: none;
  border: none;
  background: rgba(0, 0, 0, 0.05);
  padding: 8px;
  border-radius: 4px;
}

.device-emulator select {
  margin-right: 16px;
  padding-right: 16px;
}

.device-emulator input {
  width: 48px;
  height: 32px;
  margin-right: 8px;
}

.device-emulator span {
  margin-right: 8px;
}

.device-emulator input[type="checkbox"] {
  margin-left: 16px;
}

.device-emulator-container {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  overflow: auto;
}
